<script>
    import Form from "../../core/forms/form";
    import DataSelect from "../core/DataSelect";
    import MultiDataSelect from "../core/MultiDataSelect";
    import RolesSelect from "../roles/select";
    export default {
        name: "user-form",
        components: {RolesSelect, MultiDataSelect, DataSelect},
        props: [ 'edit' , 'id' ],
        data(){
            return {
                form : new Form({
                    name : '',
                    last_name : '',
                    id_no : '',
                    dob : '',
                    phone : '',
                    wallet : '',
                }),
            };
        },
        mounted() {
            if (this.edit)
            {
                this.init();
            }
        },
        methods: {
            init(){
                this.form.loading = true;
                window.axios.get(`/admin/portal/agents/${this.id}/view`).then((response) => {
                    this.form.extract(response.data.body.model);
                    this.form.loading = false;
                });
            },
            create(){
                this.form.submit(this.edit ? `/admin/portal/agents/${this.id}/update`  : '/admin/portal/agents/create').then((response) => {
                    window.alerts.success(response).then((response) => {
                        if (!this.edit)
                        this.$router.push(`/admin/agents/${response.data.body.model.id}/view`);
                    });
                }).catch((error) => {
                }).finally(() => {
                });
            }
        }
    }
</script>
<template>
    <div class="row">
        <div class="col-lg-12">
            <div :class="['dimmer' , form.loading ? 'active' : '']">
                <div class="loader"></div>
                <div class="dimmer-content">
                    <div class="card">
                        <div class="card-body">
                            <h4 class="mb-3"> {{ this.edit ? 'Edit' : 'Create' }}  User</h4>
                            <div class="form-horizontal">
                                <div class="form-group row mb-3 align-items-center">
                                    <label class="col-lg-3 col-form-label">Wallet</label>
                                    <div class="col-lg-9">
                                        <input type="text" name="name" v-model="form.wallet" :class="[ 'form-control mw-400' , form.errors.get('wallet') ? 'is-invalid' : '' ]" placeholder="Wallet">
                                        <div v-text="form.errors.get('wallet')" class="invalid-feedback"/>
                                    </div>
                                </div>
                                <div class="form-group row mb-3 align-items-center">
                                    <label class="col-lg-3 col-form-label">First Name</label>
                                    <div class="col-lg-9">
                                        <input type="text" name="name" v-model="form.name" :class="[ 'form-control mw-400' , form.errors.get('name') ? 'is-invalid' : '' ]" placeholder="First Name">
                                        <div v-text="form.errors.get('name')" class="invalid-feedback"/>
                                    </div>
                                </div>
                                <div class="form-group row mb-3 align-items-center">
                                    <label class="col-lg-3 col-form-label">Last Name</label>
                                    <div class="col-lg-9">
                                        <input type="text" name="last_name" v-model="form.last_name" :class="[ 'form-control mw-400' , form.errors.get('last_name') ? 'is-invalid' : '' ]" placeholder="Last Name">
                                        <div v-text="form.errors.get('last_name')" class="invalid-feedback"/>
                                    </div>
                                </div>
                                <div class="form-group row mb-3 align-items-center">
                                    <label class="col-lg-3 col-form-label">Phone</label>
                                    <div class="col-lg-9">
                                        <input type="text" name="phone" v-model="form.phone" :class="[ 'form-control mw-400' , form.errors.get('phone') ? 'is-invalid' : '' ]" placeholder="Phone">
                                        <div v-text="form.errors.get('phone')" class="invalid-feedback"/>
                                    </div>
                                </div>
                                <div class="form-group row mb-3 align-items-center">
                                    <label class="col-lg-3 col-form-label">ID No</label>
                                    <div class="col-lg-9">
                                        <input type="text" name="id_no" v-model="form.id_no" :class="[ 'form-control mw-400' , form.errors.get('id_no') ? 'is-invalid' : '' ]" placeholder="ID No">
                                        <div v-text="form.errors.get('id_no')" class="invalid-feedback"/>
                                    </div>
                                </div>
                                <div class="form-group row mb-3 align-items-center">
                                    <label class="col-lg-3 col-form-label">DOB</label>
                                    <div class="col-lg-9">
                                        <input type="date" name="id_no" v-model="form.dob" :class="[ 'form-control mw-400' , form.errors.get('dob') ? 'is-invalid' : '' ]" placeholder="DOB">
                                        <div v-text="form.errors.get('dob')" class="invalid-feedback"/>
                                    </div>
                                </div>

                                <div class="form-group mb-0 justify-content-end row">
                                    <div class="col-lg-9">
                                        <button type="submit" @click.prevent="create" :class="['btn btn-primary' , form.loading ? 'btn-loading' : '']">{{ this.edit ? 'Edit' : 'Create' }} User</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>

</template>
